import Swal from "sweetalert2";

export const SweetAlert = (type, text) => {
  switch (type) {
    case "success":
      Alert("Confirmacin", text || "Se ha realizado con xito!");
      break;
    case "error":
      Alert("Error", text || "Ocurri un Error!");
      break;
    case "info":
      Alert("Informacin", text || "Informacin genrica");
      break;

    default:
      break;
  }
  function Alert(title, text) {
    Swal.fire({
      title,
      text,
      icon: type,
      confirmButtonText: "OK",
    });
  }
};
